 .header__container {
     display: flex;
     align-items: center;
     justify-content: space-between;
     padding: 20px 10px;

     @media screen and (min-width: 1280px) {
         justify-content: left;


     }
 }

 .header__list {
     font-family: 'Montserrat';
     font-weight: 500;
     @include lh(22, 18, 18px);
     text-align: center;
     padding: 15px 0;
     margin: 0;


     @media screen and (min-width: 1280px) {
         margin-left: 162px;
         padding: 0;
     }
 }

 .header__item {
     &:not(:last-child) {
         margin-bottom: 45px;

         @media screen and (min-width: 1280px) {
             margin-bottom: 0;
             margin-right: 20px;
         }
     }



     transition: text-shadow 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
     cursor: pointer;

     &:hover,
     &:focus {
         text-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
     }


 }

 .nav__menu--button {
     @media screen and (min-width: 320px) {
         display: inline-flex;
         margin: 0;
         padding: 10px 0;
         z-index: 2;
         border: none;
         background-color: transparent;
         cursor: pointer;
         outline: none;
         transition: fill 250ms cubic-bezier(0.075, 0.82, 0.165, 1);

         &:hover .icon__menu,
         &:focus .icon__menu,
         &:hover .icon__cross,
         &:focus .icon__cross {
             fill: #AB816C;
         }
     }

     @media screen and (min-width: 1280px) {
         display: none;
     }
 }

 .header__list {
     @extend %list;

     @media screen and (min-width: 1280px) {
         display: flex;
     }
 }

 .icon__cross {
     opacity: 0;
     transform: translateX(-100%);

     transition: transform 250ms cubic-bezier(0.075, 0.82, 0.165, 1), opacity 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
 }

 .icon__menu {
     transition: transform 250ms cubic-bezier(0.075, 0.82, 0.165, 1), opacity 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
 }

 .nav__menu--button.is-open .icon__menu {
     opacity: 0;
     transform: translateX(100%);
 }

 .nav__menu--button.is-open .icon__cross {
     opacity: 1;
     transform: translateX(0);
 }

 .nav__menu--button.is-open~.menu__container {
     transform: translateX(0);
     visibility: visible;
     pointer-events: initial;

 }

 .menu__container {
     @media screen and (max-width: 1279.98px) {
         position: absolute;
         top: 0;
         left: 0;

         width: 100vw;
         height: 100vh;

         z-index: 1;

         padding-top: 48px;
         padding-bottom: 48px;


         display: flex;
         flex-direction: column;

         background-color: rgba(254, 243, 228, 0.9);
         transform: translateX(-100%);

         transition: transform 350ms cubic-bezier(0.075, 0.82, 0.165, 1), background-color 350ms cubic-bezier(0.075, 0.82, 0.165, 1);

     }


 }